import {GatsbyImage, IGatsbyImageData} from "gatsby-plugin-image"
import {Children, isValidElement} from "react"
import GenericLink from "~/components/UI/GenericLink/GenericLink"
import LinkedInIcon from "~/icons/LinkedInIcon"
import {classes} from "~/utilities/classes"
import "./Team.css"

interface TeamProps extends React.HTMLAttributes<HTMLDivElement> {}

interface TeamComponents {
    Member: React.VFC<TeamMemberProps>
}

const Team: React.FC<TeamProps> & TeamComponents = props => {
    const {className, children, ...otherProps} = props

    let members: Array<React.ReactElement<TeamMemberProps>> = []
    Children.forEach(children, child => {
        if (!isValidElement<TeamMemberProps>(child)) return
        members.push(child)
    })

    return (
        <div className={classes("team-853t", className)} {...otherProps}>
            <div className="grid-853t">
                {members.map((member, index) => {
                    return (
                        <figure className="member-853t" key={index}>
                            {member.props.photo && member.props.alt && (
                                <GatsbyImage image={member.props.photo} alt={member.props.alt} className="photo-853t" />
                            )}
                            <figcaption className="overlay-853t">
                                <span className="name-853t">{member.props.name}</span>
                                <span className="role-853t">{member.props.role}</span>
                                {member.props.socialLink && (
                                    <GenericLink to={member.props.socialLink} className="social-link-853t">
                                        <LinkedInIcon className="social-icon-853t" />
                                    </GenericLink>
                                )}
                            </figcaption>
                        </figure>
                    )
                })}
            </div>
        </div>
    )
}

export default Team

interface TeamMemberProps {
    name: string | null | undefined
    role: string | null | undefined
    socialLink: string | null | undefined
    photo: IGatsbyImageData | null | undefined
    alt: string | null | undefined
}

const TeamMember: React.VFC<TeamMemberProps> = props => {
    return null
}

Team.Member = TeamMember
