import {PageProps} from "gatsby"
import {GatsbySeo} from "gatsby-plugin-next-seo"
import {Fragment, useContext, useRef} from "react"
import JobApplicationForm from "~/components/Forms/JobApplicationForm"
import Approach2 from "~/components/Sections/Approach/Approach2"
import Blank from "~/components/Sections/Blank/Blank"
import Careers from "~/components/Sections/Careers/Careers"
import HeroSection from "~/components/Sections/HeroSection/HeroSection"
import Team from "~/components/Sections/Team/Team"
import Timeline from "~/components/Sections/Timeline/Timeline"
import Button from "~/components/UI/Button/Button"
import MultiStyleText from "~/components/UI/MultiStyleText/MultiStyleText"
import RichTextField from "~/components/UI/RichText/RichText"
import ScrollButton from "~/components/UI/ScrollButton/ScrollButton"
import SectionContainer, {ScrollHandle} from "~/components/UI/SectionContainer/SectionContainer"
import I18nContext from "~/contexts/I18nContext"
import {useLinksQuery} from "~/queries/LinksQuery"
import getTextFromHTML from "~/utilities/getTextFromHTML"

interface AboutPageProps extends PageProps<any, AboutUsPageContext> {}

const AboutPage: React.FC<AboutPageProps> = props => {
    const aboutPageData = props.pageContext.data

    const careersRef = useRef<ScrollHandle>(null)
    const links = useLinksQuery()
    const i18n = useContext(I18nContext)

    const scrollToCareers = (event: React.MouseEvent<HTMLButtonElement>) => {
        if (!careersRef.current) return
        careersRef.current.scrollIntoView({behavior: "smooth", inline: "start"})
    }

    return (
        <Fragment>
            <GatsbySeo
                title={aboutPageData.titolo ? aboutPageData.titolo : ""}
                canonical={`${process.env.SITE_URL}${links.about.url}`}
                description={getTextFromHTML(aboutPageData.paragrafo1?.raw)}
                language={i18n.language}
                openGraph={{
                    url: `${process.env.SITE_URL}${links.about.url}`,
                    title: `${aboutPageData.titolo}`,
                    description: `${getTextFromHTML(aboutPageData.paragrafo1?.raw)}`,
                    locale: i18n.language,
                    images: [
                        {
                            url: `${aboutPageData.header?.url}`,
                            width: Number(aboutPageData.header?.width),
                            height: Number(aboutPageData.header?.height),
                            alt: `${aboutPageData.header?.title}`,
                        },
                    ],
                }}
            />
            <HeroSection
                image={aboutPageData.header?.gatsbyImageData}
                imageAlt={aboutPageData.header?.title}
                mobileImage={aboutPageData.headerMobile?.gatsbyImageData}
            >
                <MultiStyleText flex className1="hero-title-part-1-d2bh" className2="hero-title-part-2-d2bh">
                    {aboutPageData.titolo}
                </MultiStyleText>
            </HeroSection>
            <SectionContainer tabbed marginPost customTab={<ScrollButton />} withFadeInEffect>
                <Blank>
                    <h3 className="no-margin">
                        <span className="green">{aboutPageData.titlePart1}</span>
                        {aboutPageData.titlePart2}
                    </h3>
                    <h3 className="no-margin">
                        <span className="green">{aboutPageData.titlePart3}</span>
                        {aboutPageData.titlePart4}
                    </h3>
                    <RichTextField style={{marginTop: "var(--gutter-4)"}} className="font-h6 chars-h6-80">
                        {aboutPageData.paragrafo1?.raw}
                    </RichTextField>
                    <h6 className="green">
                        <b>{aboutPageData.paragrafo2}</b>
                    </h6>
                </Blank>
            </SectionContainer>
            <SectionContainer id="timeline">
                <Timeline
                    backgroundImage={aboutPageData.roadmapBackground?.gatsbyImageData}
                    alt={aboutPageData.roadmapBackground?.title}
                >
                    {aboutPageData.roadmap &&
                        aboutPageData.roadmap.map((roadmap, index) => (
                            <Timeline.Event
                                key={index}
                                title={roadmap?.titolo}
                                description={roadmap?.descrizione}
                                icon={roadmap?.icona?.url}
                            />
                        ))}
                </Timeline>
            </SectionContainer>
            <SectionContainer id="governance" withFadeInEffect>
                <Blank>
                    <h3 className="green" style={{marginBottom: "var(--gutter-4)"}}>
                        {aboutPageData.governance?.titolo}
                    </h3>
                    <RichTextField className="chars-h6-80">{aboutPageData.governance?.paragrafo?.raw}</RichTextField>
                </Blank>
            </SectionContainer>
            <SectionContainer id="approach2">
                <Approach2>
                    {aboutPageData.governance?.approcci?.map((approccio, index) => {
                        return (
                            <Approach2.Card
                                key={index}
                                title={approccio?.titolo}
                                image={approccio?.image?.gatsbyImageData}
                                imageAlt={approccio?.image?.title}
                                url={approccio?.image?.url}
                                description={approccio?.paragrafo?.raw}
                            >
                                <Button color="green" className="approach-cta-7pwc" onClick={scrollToCareers}>
                                    {aboutPageData.lavoraConNoi?.titolo}
                                </Button>
                            </Approach2.Card>
                        )
                    })}
                </Approach2>
            </SectionContainer>
            <SectionContainer tabbed title={aboutPageData.team?.titolo} id="team">
                <Team>
                    {aboutPageData.team?.membriDelTeam?.map((member, index) => {
                        return (
                            <Team.Member
                                key={index}
                                name={member?.titolo?.toLowerCase()}
                                role={member?.posizione}
                                socialLink={member?.linkLinkedin}
                                photo={member?.immagine?.gatsbyImageData}
                                alt={member?.immagine?.title}
                            />
                        )
                    })}
                </Team>
            </SectionContainer>
            <SectionContainer ref={careersRef} id="careers" withFadeInEffect>
                <Careers
                    title={aboutPageData.lavoraConNoi?.titolo}
                    description={aboutPageData.lavoraConNoi?.paragrafo?.raw}
                    lookingFor={aboutPageData.lavoraConNoi?.jobsLookingFor}
                >
                    {aboutPageData.lavoraConNoi?.cerchiamo?.map((position, index) => (
                        <Careers.OpenPosition
                            key={index}
                            name={position?.titolo}
                            description={position?.descrizione?.raw}
                        />
                    ))}
                </Careers>
                <JobApplicationForm style={{backgroundColor: "var(--color-white)"}} />
            </SectionContainer>
        </Fragment>
    )
}

export default AboutPage

interface AboutUsPageContext {
    data: {
        id: string
        titolo?: string | null
        slug?: string | null
        node_locale: string
        paragrafo2?: string | null
        titlePart1?: string | null
        titlePart2?: string | null
        titlePart3?: string | null
        titlePart4?: string | null
        paragrafo1?: {raw?: string | null} | null
        header?: {
            title?: string | null
            url: string | null
            width: string | null
            height: string | null
            gatsbyImageData?: any | null
        } | null
        headerMobile?: {title?: string | null; url: string | null; gatsbyImageData?: any | null} | null
        roadmapBackground?: {gatsbyImageData?: any | null; title?: string | null} | null
        roadmap?: Array<{
            descrizione?: string | null
            titolo?: any | null
            icona?: {title?: string | null; url?: string | null} | null
        } | null> | null
        governance?: {
            titolo?: string | null
            paragrafo?: {raw?: string | null} | null
            approcci?: Array<{
                titolo?: string | null
                paragrafo?: {raw?: string | null} | null
                image?: {gatsbyImageData?: any | null; url?: string | null; title?: string | null} | null
            } | null> | null
        } | null
        team?: {
            titolo?: string | null
            membriDelTeam?: Array<{
                titolo?: string | null
                linkLinkedin?: string | null
                posizione?: string | null
                immagine?: {gatsbyImageData?: any | null; title?: string | null} | null
            } | null> | null
        } | null
        lavoraConNoi?: {
            titolo?: string | null
            jobsLookingFor?: string | null
            paragrafo?: {raw?: string | null} | null
            cerchiamo?: Array<{titolo?: string | null; descrizione?: {raw?: string | null} | null} | null> | null
        } | null
    }
}
